.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.coverGradient {
  /* background-image: linear-gradient(to right, rgba(215, 188, 232, 0.7) 0%, rgba(136, 132, 255, 0.9) 100%), url(./assets/images/coverPhoto2.jpeg) !important; */
  background-image: linear-gradient(to right, rgba(215, 188, 232, 0.7) 0%, rgba(136, 132, 255, 0.9) 100%) !important;
  color: white;
  height: 100vh;
  background-size: cover;
  background-position: 50% 20%;
  background-repeat: no-repeat;
  object-fit: cover;
  /* animation: pixelReveal 2s ease-out forwards; */
}

.container-fluid {
  padding-left: 0ch;
  position: relative;
  padding-right: 0ch;
  margin-left: 0ch;
  margin-right: 0ch;
}

.topNav {
  padding-top: 2%;;

  a {
    color: #F7F0F0;
    text-decoration: none;
    padding: 10px;
  }

  .navBtns{
    color: #F7F0F0;
    padding: 0% 2%;
  }
}
.bottomNav.sticky {
  position: fixed; 
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%); 
  display: flex;
  justify-content: center; 
  align-items: center; 
}

.bottomNav {
  display: none;
  padding-top: 1%;
  background-color: #1b1b1b;
  border-radius: 20px; 
  position: absolute; 
  margin-left: auto;
  padding: 5px;
  z-index: 1000;
  transition: all 0.3s ease; 
  width: auto; 
  max-width: 90%; 

  a {
    color: #F7F0F0;
    text-decoration: none;
    padding: 0px;
  }
}

h4.MuiTypography-root {
  color: snow;
}

/* ---------------animations begin-----------------------------*/

@keyframes pixelReveal {
  0% {
    clip-path: inset(0 100% 100% 0); /* Completely hidden from top-left */
    opacity: 0;
  }
  50% {
    clip-path: inset(0 50% 50% 0); /* Halfway revealed */
    opacity: 0.5;
  }
  100% {
    clip-path: inset(0 0 0 0); /* Fully revealed */
    opacity: 1;
  }
}


/* .animated-bg {
  animation: pixelReveal 2s ease-out forwards;
} */

.scroll-animation {
  transition: transform 1s ease-in;
  transform: translate(0px, 0px);
  opacity: 1;
}

.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 10px;
  background: rgba(136, 132, 255);
  border-radius: 10px;
  transform-origin: 0%;
  z-index: 1;
}


@keyframes flip {

  0%,
  80% {
    transform: rotateY(360deg)
  }
}

@keyframes headPlay {
  100% {
    transform: translateZ(0%);
    transform: scale(100%);
    transition: ease-in;
    opacity: 1;
  }
}


@keyframes projPlay {
  100% {
    transform: translateX(0%);
    transition: ease-in;
    opacity: 1;
  }
}

.headAnimation {
  animation: headPlay 1s steps(45);
  animation-iteration-count: 1;
  animation-fill-mode: both;
}

.projAnimation {
  animation: projPlay 1.5s steps(55);
  animation-iteration-count: 1;
  animation-fill-mode: both;
}

@keyframes expPlay {
  100% {
    transform: translateY(0%);
    transition: ease-in;
    opacity: 1;
  }
}

.expAnimation {
  animation: expPlay 1s steps(30);
  animation-iteration-count: 1;
  animation-fill-mode: both;
}

/* ---------------animations end-----------------------------*/

