.aboutHeading{
  background-color: #f1f1f1;
  background-image: linear-gradient(to right, #ffffff, #fffafa);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  opacity: 0;
  transform: translateZ(-30%);
  transform: scale(40%);
  align-items: center;
  padding: 3%;
}

.projHeading {
